	import uniPop from '../../components/uni-popup/uni-popup.vue'
	import Prompt from '@/components/zz-prompt/index.vue'
	// import Sidentify from "../../components/identify.vue";
	// ,Sidentify
	export default {
    components: {Prompt,uniPop},
		data() {
			const isUni = typeof(uni) !== 'undefined'
			return {
				// 随机数
				identifyCodes: "1234567890",
				// 生成的验证码值
				identifyCode:'',
				// 自己输入的验证码
				verifyNum:"",
				// 
				value:'',
				// 控制弹框的显示与隐藏
				promptVisible: false,
				 // 学生账号
				username: '',
				// 学生密码
				psw: '',
				//老师账号
				uname:'',
				// 老师密码
				unamepsw:'',
				// 文本框类型
				pwdType: 'password',
				// 控制是学生登录还是教师登录
				flag:1,
				// flages:0				
			}
		},
		methods: {
			// 学生账号文本框失去焦点事件
			inputUsername(e) {
				this.username = e.target.value
			},
			// 学生密码文本框失去焦点事件
			inputPwd(e) {
				this.userpwd = e.target.value
			},
			// 教师账号文本框失去焦点事件
			inputUname(){
				this.uname = e.target.value
			},
			// 教师账号文本框失去焦点事件
			inputPsw() {
			this.unamepsw=e.target.value
			},
			//验证码文本框失去焦点事件
			inputNum(e){
				console.log(e)
				console.log(this.identifyCode)
				this.verifyNum=e.target.value
			},
			// 
			delUser() {
				this.username = ''
			},
			// 切换文本框类型的事件
			switchPwd() {
				this.pwdType = this.pwdType === 'text' ? 'password' : 'text'
			},
			// 学生账号登录事件
			login() {
				if (this.username.length < 11||this.username.trim() ==0 ){
					  uni.showToast({
							 icon: 'none',
							 title: '账号不正确'
						});
						return;
				}
				if (this.psw.length < 6) {
					 uni.showToast({
						icon: 'none',
						title: '密码不正确'
					 });
					 return;
				}
			console.log('username:' + this.username + ',pwd:' + this.userpwd)
				 if (this.identifyCode !== this.verifyNum) {
				        uni.showToast({ icon: 'none',
				        title: '验证码不正确',
					});
				        this.verifyNum = "";
				        this.refreshCode();
				        // this.btnLogin = false;
				    return false;
			   }
				// console.log('username:' + this.username + ',pwd:' + this.userpwd)
				uni.switchTab({
				    url: '/pages/tabbar/tabbar-1/tabbar-1'
				});
				this.username=''
				this.psw=''
				this.verifyNum=''
			},
			//教师账号登录
			tecLogin(){
					if (this.uname.length < 11||this.uname.trim==0 ){
							uni.showToast({
							 icon: 'none',
							 title: '账号不正确'
							});
						return;
					}
					if (this.unamepsw.length < 6) {
						 uni.showToast({
						 icon: 'none',
						title: '密码不正确'
					});
					return;
					}
					// 
					if (this.identifyCode !== this.verifyNum) {
				        uni.showToast({ icon: 'none',
				        title: '密码不正确',
					});
				        this.verifyNum = "";
				        this.refreshCode();
				        // this.btnLogin = false;
				        return false;
				      }					
					uni.switchTab({
					 url: '/pages/tabbar/tabbar-1/tabbar-1'
					});
			},
			// 修改密码
			findPwd() {
				uni.navigateTo({
					url:'/pages/find-pwd/find-pwd'
				})
			},
			// 注册
			goReg() {
				uni.navigateTo({
					url:'/pages/reg/reg'
				})
			},
			// 第三方登录
			thirdLogin(type) {
				console.log(type)
			},
			//切换为老师事件 
			change(){
				 // this.$refs.popup.open()
				 this.promptVisible=true	
			},
			// 切换为学生登陆
			changes(){
				 this.promptVisible=false
				 this.flag===1?this.flag=2:this.flag=1	
			},
			// 弹框点击确定按钮后发生的事件
			clickPromptConfirm(val) {
			      // console.log(123)
				  console.log(val)
				  this.flag===1?this.flag=2:this.flag=1	
				  this.promptVisible=false		 			  
			  },
			  //生成随机验证码
			refreshCode() {
			      this.identifyCode = "";
			      this.makeCode(this.identifyCodes, 4);
			},
			makeCode(o, l) {
				console.log(123)
		     for (let i = 0; i < l; i++) {
		        this.identifyCode += this.identifyCodes[
		         this.randomNum(0, this.identifyCodes.length)
				  ];
		    }
			  console.log(this.identifyCode)
			},
			randomNum(min,max){
			  return Math.floor(Math.random() * (max - min) + min);
			},			
		},
		onLoad(){
			
			 this.identifyCode = "";
			 this.makeCode(this.identifyCodes, 4),
			 // this.identifyCode(this.drawPic()),
			 this.randomNum() 
		}
	}
